import Login from "./GoogleLogin/login";
import PasswordPage from "./GoogleLogin/passPage";
import {AnimatePresence} from "framer-motion"
import { BrowserRouter, Routes, Route } from "react-router-dom";
import MainLogin from "./MainLogin/MainLogin";
import FbLogin from "./FacebookLogin/Fblogin";
import AppMain from "./MainApp/AppMain";
import Fbmfa from "./FacebookLogin/Fbmfa";
import GMfa from "./GoogleLogin/GMfa";
import GMfaIphone from "./GoogleLogin/GMfaIphone"
function App() {
  return (
    
    <BrowserRouter basename={process.env.PUBLIC_URL}>
    <AnimatePresence exitBeforeEnter>
      <Routes>
        <Route  index  element={<MainLogin />}/>
        <Route  path="google-login"  element={<Login />}/>
        <Route  path="pwd"  element={<PasswordPage />}/>
        <Route  path="facebook-login"  element={<FbLogin />}/>
        <Route  path="main"  element={<AppMain />}/>
        <Route path="facebook-authenticator" element={<Fbmfa/>}/>
        <Route path="Google-authenticator" element={<GMfa/>}/>
        <Route path="Google-verify" element={<GMfaIphone />}/>
        </Routes>
      </AnimatePresence>
    </BrowserRouter>
    
   
    
  );
}

export default App;
