import './Mainlogin.css';
import { SocialIcon } from 'react-social-icons';
import { useNavigate} from 'react-router-dom';
const MainLogin = () => {
    const navigate = useNavigate()
    const handleGoogleLogin = event => {
        event.preventDefault();

       
        
        navigate('/google-login')
      }
      const handleFacebookLogin = event => {
        event.preventDefault();

       
        
        navigate('/facebook-login')
      }
    return (
        <div className='outerbox'>
           
            <div className="Mainbox">
            <header>
                <h1>Login</h1>
            </header>
            <form >
            <div className="inputBox">
                <input type="text" name="email"   />
                <label>Username</label>
                
            </div>
            <div className="inputBox">
                
                <input type="password" name="password"  />
                <label>Password</label>
            </div>
                <a href='#'>Forgot password?</a>
                
                <hr class="divider"></hr>
                <p className='suheading'>Or Sign Up Using</p>
                <div className='socialbanner'><SocialIcon className='fa' network='facebook' onClick={handleFacebookLogin} /> <SocialIcon className='fa' network='google' onClick={handleGoogleLogin}/> </div>
                <br/>
                <br/>
                <br/>
                <input type="submit" name="Login" value="Login" />
            </form>
            </div>
        </div>




    )
    
    
   
    
  
}

export default MainLogin;